<template>
<keep-alive>
  <div class='topListCon'>
    <div class="topLis">
      <ol class="switch-tab inner">
        <li class="current">
          <a>排行榜</a>
        </li>
      </ol>
    </div>
    <div class="gameListCon inner" v-loading = 'loading'>
        <game-list v-if="gameList.length>0" :GameList ='gameList' />
      <!-- <button class="watchMore" @click="loadData()">查看更多</button> -->
    </div>
  </div>
</keep-alive>

</template>
<script>
import axios from 'axios'
import gameList from '@/components/gameList'
export default {
  components: {
    gameList
  },
  data: function () {
    return {
      gameList: [],
      loading: false
    }
  },
  created () {
    console.log(this.$route.query.type)
    this.loadData()
  },
  methods: {
    loadData () {
      this.loading = true
      axios.get('../../../static/Json/top_list.json').then(res => {
        this.loading = false
        this.gameList = res.data
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.topListCon {
  background: #f5f5f5;
  padding: 20px;
  .switch-tab {
    text-align: center;
    height: 32px;
    line-height: 32px;
    border-bottom: 2px solid #4cc8b5;
    overflow: hidden;
    margin: 0 auto;
    padding-top: 10px;
    li {
      text-align: center;
      display: inline-block;
      a {
        font-size: 14px;
        display: inline-block;
        color: #666;
        padding: 0 17px;
      }
      &.current a {
        color: #fff;
        background: #4cc8b5;
        font-size: 0.875em;
      }
    }
  }
}
.gameListCon {
  padding: 0 20px 70px 0;
  position: relative;
}
.watchMore {
  margin: 23px 0;
  position: absolute;
  left: 50%;
  margin-left: -48px;
  color: #333333;
  background: #ddd;
  border: none;
  line-height: 35px;
  font-size: 14px;
  width: 96px;
}
</style>



